import React from 'react';
import { Container, Typography, Box, Button } from '@mui/material';
import { useLocation, useNavigate } from 'react-router-dom';

const AppointmentNotifications: React.FC = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const booking = location.state?.booking; // Ly thng tin booking t state ca navigate

  const handleViewMyBookings = () => {
    if (booking?.customer?.id) {
      navigate(`/my-bookings/${booking.customer.id}`);
    } else if (booking?.email) {
      // Fallback: if customer ID not directly available, try to navigate to general my-bookings.
      // In a real app, you might want to fetch customer ID by email or require login.
      navigate('/my-bookings');
    } else {
      navigate('/my-bookings'); // Navigate to default my-bookings if no specific ID or email
    }
  };

  return (
    <Container sx={{ py: 8 }}>
      <Typography variant="h4" component="h1" gutterBottom>
        Thng bo lch hn
      </Typography>
      <Box>
        {booking ? (
          <>
            <Typography variant="h6" component="h2" gutterBottom>
              Lch t ca bn  thnh cng!
            </Typography>
            <Typography variant="body1"><strong>Dch v:</strong> {booking.service?.name || booking.service}</Typography>
            <Typography variant="body1"><strong>Ngy:</strong> {new Date(booking.bookingDate).toLocaleDateString('vi-VN')}</Typography>
            <Typography variant="body1"><strong>Thi gian:</strong> {booking.displayTime || booking.bookingTime}</Typography>
            <Typography variant="body1"><strong>Tn khch hng:</strong> {booking.customer?.fullName || booking.name}</Typography>
            <Typography variant="body1"><strong>S in thoi:</strong> {booking.customer?.phoneNumber || booking.phone}</Typography>
            <Typography variant="body1"><strong>Email:</strong> {booking.customer?.email || booking.email}</Typography>
            {booking.notes && <Typography variant="body1"><strong>Ghi ch:</strong> {booking.notes}</Typography>}
            <Typography variant="body1"><strong>Trng thi:</strong> {booking.status === 'CONFIRMED' ? ' xc nhn' : booking.status === 'PENDING' ? 'Ch xc nhn' : booking.status === 'CANCELLED' ? ' hy' : booking.status}</Typography>
            <Box mt={4}>
              <Button
                variant="contained"
                color="primary"
                onClick={handleViewMyBookings}
              >
                Xem lch ca ti
              </Button>
            </Box>
          </>
        ) : (
          <>
            <Typography variant="body1">
              y l trang hin th thng bo v lch hn. Khng c thng tin lch hn c th.
            </Typography>
            <Box mt={4}>
              <Button
                variant="contained"
                color="primary"
                onClick={handleViewMyBookings}
              >
                Xem lch ca ti
              </Button>
            </Box>
          </>
        )}
      </Box>
    </Container>
  );
};

export default AppointmentNotifications; 